nav {
    width: 100%;
    /*background: #333;*/
    color: #fff;
    padding: 4px 0;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    z-index: 10;
}

.logo {
    width: 180px;
}

nav ul li {
    display: inline-block;
    list-style: none;
    margin: 5px 20px;
    font-size: 16px;
}

.darknav {
    background: #212EA0;
    transition: 0.5s;
}

.menuicon {
    display: none;
}

/*media*/
@media (max-width:1000px) {
    .logo {
        width: 140px;
    }
    nav ul li {
        margin: 10px 15px;
    }
}
@media (max-width:856px) {
    nav {
        padding: 15px 0;
    }
    nav ul {
        position: fixed;
        top: 0;
        right: 0px;
        background: #212EA0;
        z-index: -1;
        width: 200px;
        padding-top: 70px;
        transition: 0.5s;
        bottom: 0;
    }
    nav ul li {
        display: block;
        margin: 25px 40px; 
    }
    .menuicon {
        display: block;
        width: 30px;
        cursor: pointer;
    }
    .hidemenumob {
        right: -200px;
    }
}


