.hero {
    width: 100%;
    min-height: 100vh;
    background:linear-gradient(rgba(8,0,58,0.7),rgba(8,0,58,0.7)), url('../assests/hero.png');
    background-size: cover;
    background-position: center;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;

}

.herotext {
    text-align: center;
    max-width: 800px;
}

.herotext h1 {
    font-size: 60px;
    font-weight: 600;
}

.herotext p {
    max-width: 700px;
    margin: 10px auto 20px;
    line-height: 1.4;
}

/*media*/
@media (max-width:850px) {
    .herotext h1 {
        font-size: 40px;
    }
}
@media (max-width:650px) {
    .herotext h1 {
        font-size: 30px;
        max-width: 400px;
        margin: auto;
    }
    .herotext p {
        font-size: 14px;
        margin: 15px auto 30px;
    }
}