.about {
    margin: 100px auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
}

.aboutleft {
    flex-basis: 40%;
    position: relative;
}

.aboutright {
    flex-basis: 56%;
}

.aboutimg {
    width: 100%;
    border-radius: 10px;
}

.playicon {
    width: 60px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
}

.about h3 {
    font-weight: 600;
    font-size: 16px;
    color: #212EA0;
}

.about h2 {
    font-weight: 600;
    font-size: 35px;
    color: #000;
    margin: 10px 0;
    max-width: 400px;
}

.about p {
    color: #676767;
    margin-bottom: 15px;
}

/*media*/ 
@media (max-width:800px) {
    .about {
        flex-direction: column;
    }
    .aboutleft {
        flex-basis: 100%;
        margin: 20px;
        order: 2;
    }
    .aboutright {
        flex-basis: 100%;
    }
}